@import url(https://fonts.googleapis.com/css?family=Inconsolata:400,700);
*{box-sizing:border-box}html,body,#root,.App,.Requests,.Request{height:100%;overflow-y:scroll}.App{padding-top:3.5em}h1{position:absolute;background:#fff;width:100%;top:0;margin:0;font-size:1rem;padding-left:1rem;padding-top:1rem}.flex{height:100%}body{font-family:"Inconsolata", monospace;margin:auto;height:100%}.Request,.Requests{overflow-y:scroll}.flex{display:flex}h1,h2,h3{font-family:"Helvetica Neue", sans-serif;letter-spacing:-0.05em}h1 a{color:inherit}header a{font-weight:bold;font-family:"Helvetica Neue", sans-serif;font-size:2em;text-decoration:none;color:inherit;letter-spacing:-2px}.recent{display:none}.recent-link{display:block;padding:1em 2em;margin-bottom:1em;color:inherit;text-decoration:none;border:1px solid #eee}header{padding:2em 0}@media all and (max-width: 30em){.headers,.payload{float:none;width:100%;padding:0}.meta abbr{margin-top:0.5em;display:block}}.request{border:1px solid #eee;margin-top:2em;border-radius:3px;padding:1em}.body{font-size:1.5em;line-height:2;margin-top:0}.copy{padding:0.5em 1em;font:inherit;font-size:1.5em;display:block;width:100%;margin-bottom:1.5em}.meta{background:#eee;margin-left:-1em;margin-right:-1em;margin-top:-1em;display:flex;justify-content:space-between;align-items:center;padding:1em}.meta strong{text-transform:uppercase}.meta .show-if-proxy{float:right;text-decoration:none}.request:after{content:" ";display:table;clear:both}[data-status="error"]{background:#b83635;margin:1em 0;padding:1em}[data-status="success"]{background:#2c962c;margin:1em 0;padding:1em}.connection-result{position:relative;color:#fbf8f8}.remove{float:right;position:absolute;right:0.5em;top:50%;color:#fbf8f8;font-size:2em;-webkit-transform:translateY(-50%);transform:translateY(-50%);text-decoration:none}#proxy ~ .show-if-proxy,#proxy ~ .requests .show-if-proxy{display:none}#proxy:checked ~ .show-if-proxy,#proxy:checked ~ .requests .show-if-proxy{display:block}.collapse{background:white;display:inline-block;height:1em;width:1em;text-align:center;font-size:2rem;cursor:pointer}

.Requests{padding-right:2em;flex:1 1;width:300px;height:100%}.Requests h2{padding-left:1rem}.Requests .enter,.Requests .enter-done{-webkit-transition:all 1s ease;transition:all 1s ease}.Requests .soft{opacity:0.5}.Requests .enter-active{background:#ffcd42;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.Requests ul{list-style:none;padding:0;margin:0;text-align:left}.Requests ul li{border-bottom:1px solid white;cursor:pointer}.Requests ul li a{padding:0.5em 1em;display:block}.Requests ul li pre{text-overflow:ellipsis;overflow:hidden}.Requests ul li:hover{background:#f6f6f6}.Requests ul li a:not(.try-it){color:inherit}.Requests ul li.active{background:#ebebeb;position:relative}.Requests ul li.active:before{content:"";display:block;position:absolute;right:-20px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);height:0;width:0;border-color:transparent transparent transparent #ebebeb;border-width:10px;border-style:solid}.Requests ul li a{text-decoration:none}.Requests ul li pre{margin:0}.Requests ul li time{font-size:0.7em}

.Request{flex:4 1 !important;padding-left:1em}.Request h2,.Request h2>pre{margin:0}.Request .headers,.Request .payload,.Request pre{white-space:normal}.Request .react-json-view{background:white !important;font-family:Inconsolata, monospace !important;padding:1em;word-break:break-all}

.NewRequest{flex:1 1}.NewRequest script{color:black;padding:1em;margin:0;white-space:pre;margin-bottom:1em}.NewRequest pre{padding:0.25em}.NewRequest script,.NewRequest pre{font-size:0.8rem;border:1px solid #ebebeb;border-radius:3px}.NewRequest pre.bash{position:relative}.NewRequest pre.bash:before{content:"$ "}.NewRequest h3{margin-top:0}.NewRequest button{background-color:limegreen;border:none;font:inherit;color:#fff;padding:1em 2em;text-decoration:none;font-weight:bold;display:block;cursor:pointer;text-shadow:1px 1px 0 #647b1b}.NewRequest pre.wrap{white-space:pre-wrap;word-break:break-all}

